import React from "react";
import StyledLink from "../../Link";
import "./Menu.scss";

const Menu = ({ menu }) => (
  <nav className="Menu">
    <ul className="Menu-list">
      {menu.map(item => (
        <li className="Menu-listItem" key={item.path}>
          <StyledLink variation={"tertiaryAltInverse"} isActive path={item.path}>
            {item.label}
          </StyledLink>
        </li>
      ))}
    </ul>
  </nav>
);

export default Menu;
